:root {
  --surface-color: $white;
  --curve: 40;
}

* {
  box-sizing: border-box;
}

body {
  background-color: rgb(25, 98, 25);
}

.container {
  background-color: Black;
}

.img {
  width: 100%;
  height: auto;
}

.btn-primary {
  background-color: greenyellow ;
  border-color: red ;
  &:hover {
    background-color: red;
    border-color: greenyellow ;
  }
}
.btn-outline-primary {
  color: black;
  border-color: black;
  &:hover {
    color: purple;
    background-color: purple;
  }
}
.button {
  background-color: green;
  border-color: red;
  &:hover {
    background-color: green;
    border-color: red;
  }
}

.button-group {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.card {
  color: white;
  margin-bottom: 2rem;
}

.card-header {
  padding: 2em;
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-height: 250px;
}

.card-body {
  background-color: rgb(25, 98, 25);
  color: white;
  padding: 1rem 2rem 2rem;
  max-height: 250px;
  overflow: hidden;
}

.card-footer {
  text-align: center;
  background-color: white;
  border: 3rem;
}
